.m{
    width: 15rem;
    margin: 0 auto;
}
html{
    font-size: 20px;
}
.customization{
    margin-top: 0.5rem;
    margin-bottom: 1.6rem;
}
/*寮规*/
.customization .shade{
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,.7);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    display: none;
}
.customization .shade .box{
    height: 11.25rem;
    background-color: #fff;
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
.customization .shade .box img{
    width: 100%;
    height: 100%;
}
.customization .shade .box s{
    position: absolute;
    width: 1.125rem;
    height: 1.275rem;
    background: url("https://static.blove.com/Mobile2017/images/CustomStory/x.png") no-repeat;
    background-size: cover;
    top: 0.5rem;
    right: 0.75rem;
}
/*鍒楄〃*/
.customization .customlist ul li{
    width: 100%;
    margin-bottom: 1.5rem;
}
.customization .customlist ul li .pic{
    width: 100%;
    height: 10rem;
    position: relative;
}
.customization .customlist ul li .pic .show{
    width: 100%;
    height: 100%;
}
.customization .customlist ul li .pic .show img{
    width: 100%;
    height: 100%;
}
.customization .customlist ul li .pic .sort{
    width: 3rem;
    height: 0.95rem;
    background: url("https://static.blove.com/Mobile2017/images/CustomStory/sort.png") no-repeat;
    background-size: cover;
    position: absolute;
    top: 0.75rem;
    right: -0.25rem;
    color: #fff;
    line-height: 0.65rem;
    font-size: 0.5rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding-left: 0.5rem;
}
.customization .customlist ul li .pic .breviary{
    width: 5.75rem;
    height: 5.75rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border: 1px solid #ff3366;
    border-radius: 50%;
    position: absolute;
    bottom:0;
    left: 50%;
    -webkit-transform: translate(-50%,50%);
            transform: translate(-50%,50%);
}
.customization .customlist ul li .pic .breviary img{
    display: block;
    width: 5.35rem;
    height: 5.35rem;
    margin: 0.2rem;
}
.customization .customlist ul li .pic .breviary s{
    position: absolute;
    width: 1.1rem;
    height: 1.1rem;
    border-radius: 50%;
    background-color: #ff3366;
    color: #fff;
    text-align: center;
    line-height: 1rem;
    font-size: 1rem;
    top: 0.3rem;
    left: 0.4rem;
}
.customization .customlist ul li .txt{
    margin-top: 3.25rem;
    text-align: center;
}
.customization .customlist ul li .txt .title h3{
    color: #666666;
    font-size: 1rem;
    font-weight: normal;
}
.customization .customlist ul li .txt .title h5{
    color: #666666;
    font-size: 0.65rem;
    font-weight: normal;
    line-height: 1rem;
}
.customization .customlist ul li .txt .title h5 img{
    width: 0.5rem;
    height: 0.565rem;
    vertical-align: middle;
    margin:0 0.2rem;
}
.customization .customlist ul li .txt .content{
    margin-top: 0.3rem;
}
.customization .customlist ul li .txt .content p{
    font-size: 0.5rem;
    color: #b7b7b7;
    line-height: 0.8rem;
}
.customization .customlist ul li .txt .content a{
    display: block;
    width: 3.875rem;
    height: 1.15rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border: 1px solid #ff3366;
    margin: 0.5rem auto 0;
    font-size: 0.6rem;
    text-align: center;
    color: #ff3366;
    line-height: 1.1rem;
    letter-spacing: 2px;
}
/*鍒嗛〉*/
.customization .page{
    height: 1.7rem;
    background-color: #fff7f8;
    text-align: center;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-size: 0;
    padding-top: 0.3rem;
}
.customization .page ul{
    font-size: 0;
    margin: 0 auto;
}
.customization .page ul li{
    float: left;
}
.customization .page ul li.disabled a{
    display: inline-block;
    width: 1.1rem;
    height:1.1rem;
    background-color: #ff3366;
    color: #fff;
    line-height: 1.1rem;
    text-align: center;
    font-family: SimSun;
    font-size: 0.8rem;
    font-weight: 900;
    border-radius: 0.2rem;
}
.customization .page  ul li.disabled:first-child{
    margin-right: 0.3rem;
}
.customization .page ul li.disabled:last-child{
    margin-left: 0.3rem;
}
.customization .page ul li.active a{
    display: inline-block;
    width: 0.85rem;
    height: 0.85rem;
    text-align: center;
    line-height: 0.85rem;
    font-size: 0.5rem;
    vertical-align: top;
    margin: 0.125rem;
    border-radius: 0.2rem;
}
.customization .page ul li.active a.check{
    background-color: #ff3366;
    color: #fff;
    font-weight: 700;
}
/* 分页 */
.page-bar{
    margin:40px;
}
ul,li{
    margin: 0px;
    padding: 0px;
}
li{
    list-style: none
}
.page-bar li:first-child>a {
   margin-left: 0px
}
.page-bar a{
    border: 1px solid #ddd;
    text-decoration: none;
    position: relative;
    float: left;
    padding: 6px 12px;
    margin-left: -1px;
    line-height: 1.42857143;
    color: #337ab7;
    cursor: pointer
}
.page-bar a:hover{
    background-color: #eee;
}
.page-bar a.banclick{
    cursor:not-allowed;
}
.page-bar .active a{
    color: #fff;
    cursor: default;
    background-color: #337ab7;
    border-color: #337ab7;
}
.page-bar i{
    font-style:normal;
    color: #d44950;
    margin: 0px 4px;
    font-size: 12px;
}

